<template lang="html">
  <div class="hbox animated fadeIn m-n">
    <div class="col w">
      <div class="vbox">
        <div class="row-row">
          <ul class="list-group">
            <draggable v-model="btns" class="dragArea" :options="{group:{ name:'people',  pull:'clone', put:false }}">
              <transition-group>
                <li class="list-group-item" v-for="(r, idx) in btns" :key="'r_'+idx">{{r.l}}</li>
              </transition-group>
            </draggable>
          </ul>
        </div>
      </div>
    </div>
    <div class="col">
      <div class="row m-n h-full">
        <div class="col-md-6 h-full">
          <div class="b center-block" style="width:337px">
            <table cellspacing="0" cellpadding="0" class="ode-SimpleMockFormPhoneBar" style="width: 100%; height: 24px;">
              <tbody>
                <tr>
                  <td align="right" width="" height="" rowspan="1" style="vertical-align: top;">
                    <img width="150" height="24" src="data:image/png;base64," ></img>
                  </td>
                </tr>
              </tbody>
            </table>
            <div class="ode-SimpleMockFormTitleBar" style="position: relative; overflow: hidden; width: 100%; height: 24px;">
              <div class="ode-SimpleMockFormTitle" style="text-align: left; position: absolute; left: 12px; top: 4px;">Screen1</div>
            </div>
            <div style="overflow: auto; position: relative; zoom: 1; width: 337px; height: 422px;">
              <div style="position: relative; zoom: 1;">
                <div style="position: relative; overflow: hidden; background-color: rgb(255, 255, 255); background-image: url(&quot;&quot;); background-repeat: no-repeat; background-position: center center; background-size: 100% 100%; width: 320px; height: 422px;">
                  <draggable v-model="btns2" class="dragArea h-full" :options="{group:'people'}">
                    <li class="list-group-item" v-for="(r, idx) in btns2" :key="'r_'+idx">{{r.l}}</li>
                  </draggable>
                </div>
              </div>
            </div>
            <table cellspacing="0" cellpadding="0" class="ode-SimpleMockFormPhoneBar" style="width: 100%; height: 35px;">
              <tbody>
                <tr>
                  <td align="center" style="vertical-align: top;">
                    <img width="239" height="35" src="data:image/png;base64,"></img>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="col-md-6">
          <textarea class="w-full" rows="20"></textarea>
        </div>
      </div>
    </div>
    <!-- <div class="col w">
      <div class="vbox">
        <div class="row-row">
          <div class="cell">
            <ul class="list-group">
              <li class="list-group-item" v-for="v in btns">{{v.l}}</li>
            </ul>
          </div>
        </div>
      </div>
    </div> -->
  </div>
</template>

<script>
import Draggable from 'vuedraggable'
import { buttonGroup, dropdown, radio, aside as sidebar } from 'vue-strap'
import hub from '@/utils/eventhub'
import lai from './ItemLAI'
// require('../../../static/ya.css')
export default {
  name: 'Drag',
  data () {
    return {
      btns: [
        { l: '格式化', v: 'fmt' },
        { l: '编/解码', v: 'codec' },
        { l: 'Hash', v: 'hash' },
        { l: 'Hmac', v: 'hmac' },
        { l: '加/解密', v: 'cipher' }
      ],
      btns2: [],
      curr: ''
    }
  },
  watch: {
    btns (nv) {
      console.log(nv)
      console.log(this.curr)
    }
  },
  components: {
    radio,
    dropdown,
    buttonGroup,
    Draggable,
    sidebar,
  },
  mounted () {
    hub.$emit('aside')
  },
  beforeDestroy () {
    hub.$emit('aside')
  }
}
</script>

<style lang="css">
.dragArea {
  min-height: 100px;
  background-color: #ccc;
}

.center-block {
  display: block;
  margin-right: auto;
  margin-left: auto;
}

.ode-SimpleMockFormPhoneBar {
  background-color: #000000;
}

.ode-SimpleMockFormTitleBar {
  background-color: dimgray;
  border-bottom: 1px solid lightgray;
}

.ode-SimpleMockFormTitle {
  color: white;
  font-size: 90%;
}

</style>



