<template lang="html">
  <div class="">
    <tree :model="d"/>
  </div>
</template>

<script>
import tree from '@/utils/tree'
export default {
  name: 'opts',
  components: {
    tree
  },
  data () {
    return {
      d : {
        name: 'My Tree',
        children: [
          { name: 'hello' },
          { name: 'wat' },
          {
            name: 'child folder',
            children: [
              {
                name: 'child folder',
                children: [
                  { name: 'hello' },
                  { name: 'wat' }
                ]
              },
              { name: 'hello' },
              { name: 'wat' },
              {
                name: 'child folder',
                children: [
                  { name: 'hello' },
                  { name: 'wat' }
                ]
              }
            ]
          }
        ]
      }
    }
  }
}
</script>

<style lang="css">
</style>



