<template lang="html">
  <div class="">
    <div class="input-group">
      <span class="input-group-addon">{{lb}}</span>
      <input type="text" class="form-control" :placeholder="ph">
    </div>
  </div>
</template>

<script>
export default {
  model: {
    prop: 'modelValue',
    event: 'input'
  },
  name: 'lai',
  props: {
    lb: {type: String, default: 'label'},
    ph: {type: String, default: 'dft'}
  }
}
</script>

<style lang="css">
</style>



